html{
    font-family: 'Roboto Mono', monospace;
    /* background-color:rgb(6, 36, 70); */
    width: 100%;
    overflow: hidden;
    background-image: url('assets/fondo3.jpg');
    background-size: cover;
}
body{
    margin: 0px;
}
.container{
    display: flex;
    justify-content: center;
}   
.presentation{
    width: 100%;
    height:430px
}
.arrow{
    color:#c4c8c5; 
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}
.arrow:hover{
    color:#466067

}
.apps{
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

}

.Contenedor{
    height: 100vh;
    width: 100%;
    position: relative;
    background: rgb(0,0,0,0.5);
}
