.container-navbar{
    width: 100%;
}
.navbar-align{
display: flex;
justify-content: center;
align-items: center;
width: 100%;
height: 80px;

}
.items-navbar-style{
    display:flex;
    gap:15px;
    height: 100%;
    align-items: center;
}
.items-navbar-style a{
    text-decoration: none;
    font-size: 14px;
    cursor:pointer;
    color: #F6F0E2;
    border: 1px solid #F6F0E2;
    height: 30px;
    width: 116px;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
    border-radius: 15px;
}
.CV{
    position: absolute;
    right: 25px;
    text-decoration: none;
    font-size: 14px;
    cursor: pointer;
    color: black;
    border: solid 2px #F6F0E2;
    border-radius: 15px;
    width: 120px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F6F0E2;
}
.CV:hover{
    border: solid 3px #3c3c3c;
    background-color: white;
    color: #3c3c3c;
    font-weight: 600;
}
.items-navbar-style a:hover{
    color:#c4c8c5;
}
.container-logo{
    display: flex;
}
.container-logo img{
    width: 150px;
    height: 100px;
}
.container-presentation-text{
    position: absolute;
    left: 50%;
    top: 45%;
    transform: translate(-50%, -50%);
    width: 1000px;
    height: 300px;
    text-align: center;
    background-color: rgba(177,177,177,0.2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    border:5px solid white;
    border-radius: 5px;

}

.container-presentation-text h4{
    font-size: 18px;
    color: white;
    font-weight: 100;
    margin: 0px;
}

.container-presentation-text h1{
    font-size: 80px;
    margin: 0px;
    color:white;
}
.container-presentation-text h3{
    font-size: 40px;
    margin: 0px;
    color:white;
}


  .cursor {
    display: inline-block;
    font-size: 17px;
    color: inherit; /* Cambia el color seg√∫n tus necesidades */
    animation: parpadeo 1s infinite;
    margin:0px; 
  }

  .description{
    display: flex;
    margin-top: 15px;
    width: 75%;
}
.description span {
    font-size: 18px;
    color:#e6fcfc;
}
.colombia{
    background: linear-gradient(to right, yellow 9.33%, yellow 14.66%, #0033A0 55.66%, #0033A0 16%, #FF0000 85%) !important;
    -webkit-background-clip: text !important; 
    font-weight: bold !important; /* Puedes ajustar el peso de la fuente si es necesario */
    color: transparent !important;
}
.linterna {
    position: absolute;
    width: 700px;
    height: 700px;
    border-radius: 50%;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0) 80%);
    pointer-events: none;
    transform: translate(-50%, -50%);
    
}
.instagram, .linkedin, .github{
    color:black;
    /* border-radius: 50%; */
}
.instagram:hover, .linkedin:hover, .github:hover{
    color: gray;
    cursor: pointer;
}
.iconos{
    gap: 40px;
    font-size: 24px;
    color: white;
    display: flex;
    justify-content: center;
    position: absolute;
    /* flex-direction: column; */
    /* height: 100vh; */
    /* background: rgba(255,255,255,0.7); */
    /* width: 58px; */
    align-items: center;
    left: 50%;
    bottom: 98px;
    transform: translate(-50%, -50%);

}
.iconos a{
    width: 40px;
    height: 40px;
    background: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.imagen{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.section-image{
    display: flex
}
.container-imagen{
    width: 400px;
    height: 400px;
    position: absolute;
    bottom: 14%;
    right: 1%;
}

